<template>
  <div style="padding: 10px">
<!--    功能区域-->
    <div style="margin: 10px 0">
      <el-button type="primary" @click="add">新增</el-button>
    </div>
<!--    搜索区域-->
    <div style="margin: 10px 0">
      <el-input
          style="width: 20%"
          v-model="search"
          size="large"
          placeholder="请输入关键字"
          clearable
      />
      <el-button type="primary" style="margin-left: 5px" @click="load">查询</el-button>
    </div>
    <el-table :data="tableData" stripe border  style="width: 100%">
      <el-table-column prop="userid" label="ID" sortable  />
      <el-table-column prop="username" label="姓名"  />
<!--      数据库里是下划线 对应前台代码会转成驼峰 mybatisplus这个框架帮做的-->
      <el-table-column prop="telephone" label="电话"  />
      <el-table-column prop="age" label="年龄" />
      <el-table-column prop="sex" label="性别" />
      <el-table-column prop="address" label="地址" />
      <el-table-column fixed="right" label="操作" width="120">
        <template #default="scope">
          <el-button link type="primary" size="small" @click="handleEdit(scope.row)">编辑</el-button>
          <el-popconfirm title="确认删除吗？" @confirm="handleDelete(scope.row.userid)">
            <template #reference>
              <el-button link type="danger" size="small" >删除</el-button>
            </template>
          </el-popconfirm>

        </template>
      </el-table-column>
    </el-table>

    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          :page-sizes="[5, 10, 20]"
          :page-size="pageSize"
          :disabled="disabled"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />

<!--      弹窗-->
      <el-dialog
          v-model="dialogVisible"
          title="提示"
          width="30%"
      >
        <el-form :model="form" label-width="120px">
          <el-form-item label="用户名">
            <el-input v-model="form.username" style="width: 70%" />
          </el-form-item>
          <el-form-item label="电话">
            <el-input v-model="form.telephone" style="width: 70%" />
          </el-form-item>
          <el-form-item label="年龄">
            <el-input v-model="form.age" style="width: 70%" />
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="form.sex">
              <el-radio label="男" size="large">男</el-radio>
              <el-radio label="女" size="large">女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="职位">
            <el-input v-model="form.position" style="width: 70%" />
          </el-form-item>
          <el-form-item label="地址">
            <el-input type="textarea" v-model="form.address" style="width: 70%" />
          </el-form-item>
        </el-form>

        <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="save">确定</el-button>
        <el-button @click="dialogVisible = false">取消</el-button>
      </span>
        </template>
      </el-dialog>
    </div>
  </div>
</template>

<script>


import request from "@/utils/request";

export default {
  name: 'User',
  components: {
  },
  data(){
    return{
      form:{},
      dialogVisible:false,
      search:'',
      currentPage:1,
      pageSize:10,
      total:0,
      tableData:[]
    }
  },
  created() {

    this.load()
  },
  methods:{
    load(){
      request.get("/user",{
        params:{
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          search: this.search
        }
      }).then(res =>{
        console.log(res)
        this.tableData = res.data.records
        this.total = res.data.total
      })
      console.log(this.form)
    },
    add(){
      this.dialogVisible = true;
      this.form = {}
    },
    save(){
      if(this.form.userid){//更新
        request.put("/user",this.form).then(res =>{//.then是es6里的语法
          console.log(res)
          if(res.code == '0'){
            this.$message({
              type:"success",
              message:"更新成功！"
            })
          }else{
            this.$message({
              type:"error",
              message: res.msg
            })
          }
          this.load()//刷新表格数据
          this.dialogVisible = false;//关闭弹窗
        })
      }else{//插入
        request.post("/user",this.form).then(res =>{//.then是es6里的语法
          console.log(res)
          if(res.code == '0'){
            this.$message({
              type:"success",
              message:"新增成功！"
            })
          }else{
            this.$message({
              type:"error",
              message: res.msg
            })
          }
          this.load()//刷新表格数据
          this.dialogVisible = false;//关闭弹窗
        })
      }

    },
    handleEdit(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },
    handleDelete(id){
      console.log(id)
      request.delete("/user/"+id).then(res =>{
        if(res.code == '0'){
          this.$message({
            type:"success",
            message:"删除成功！"
          })
        }else{
          this.$message({
            type:"error",
            message: res.msg
          })
        }
        this.load()//重新加载
      })
    },
    handleSizeChange(pageSize){ //改变当前页的个数触发
      this.pageSize = pageSize;
      this.load()
    },
    handleCurrentChange(pageNum){ // 改变当前页码触发
      this.currentPage = pageNum
      this.load()
    }
  }
}
</script>
